.button {
  padding: 1.3rem 2.2rem;
  font-size: 1.7rem;
  cursor: pointer;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  display: inline-block;
  border-radius: 4rem;
  margin: 0 .5rem;
